@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/firasans-regular-webfont.woff2") format("woff2"), url("../fonts/firasans-regular-webfont.woff") format("woff"); }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/firasans-medium-webfont.woff2") format("woff2"), url("../fonts/firasans-medium-webfont.woff") format("woff"); }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/firasans-bold-webfont.woff2") format("woff2"), url("../fonts/firasans-bold-webfont.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold-webfont.woff2") format("woff2"), url("../fonts/montserrat-bold-webfont.woff") format("woff"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/raleway-extrabold-webfont.woff2") format("woff2"), url("../fonts/raleway-extrabold-webfont.woff") format("woff"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/raleway-black-webfont.woff2") format("woff2"), url("../fonts/raleway-black-webfont.woff") format("woff"); }

body {
  min-width: 320px; }

.hidden {
  display: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 8% 0;
  font-family: "Fira Sans";
  font-weight: 500;
  font-size: 15px;
  line-height: 18px; }
  .page-header a {
    margin: 0;
    padding: 0;
    color: #484848;
    text-decoration: none; }

.page-header__call-order {
  text-transform: uppercase;
  border-bottom: 1px dashed #484848; }

@media (max-width: 767px) {
  .page-header {
    flex-wrap: wrap;
    flex-direction: column;
    padding: 20px 8% 0;
    font-size: 14px;
    line-height: 17px; }
  .page-header__contact-tel {
    margin-left: auto;
    margin-bottom: 12px;
    order: 1; }
  .page-header__call-order {
    margin-left: auto;
    order: 2; }
  .page-header__logo-wrapper {
    position: relative;
    margin-right: auto; }
  .page-header__logo {
    position: absolute;
    width: 120px;
    height: 42px; } }

.page-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 8%; }

.page-footer__copyright {
  margin: 0;
  font-family: "Fira Sans";
  font-weight: 400;
  font-size: 12px;
  line-height: 150%;
  text-align: center;
  color: #484848;
  opacity: 0.5; }

@media (max-width: 767px) {
  .page-footer {
    display: flex;
    flex-direction: column;
    padding-top: 32px;
    padding-bottom: 32px; }
  .page-footer__logo-wrapper,
  .page-footer__copyright {
    margin-bottom: 24px; }
  .page-footer__logo {
    width: 99px;
    height: 31px; } }

.social__list {
  display: flex;
  justify-content: space-between;
  width: 112px;
  margin: 0;
  padding: 0;
  list-style: none; }

@media (max-width: 767px) {
  .social__link-icon {
    width: 32px;
    height: 32px; } }

.general-info__header {
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 40px;
  color: #1FC0FC;
  line-height: 49px; }

.call-form-modal {
  position: relative;
  width: 480px;
  height: 521px;
  margin: 0 auto;
  padding: 56px 72px 40px 48px;
  border: 1px solid transparent;
  box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  box-sizing: border-box; }

.call-form-modal__btn-close {
  position: absolute;
  top: 32px;
  right: 32px;
  width: 18px;
  height: 18px;
  font-size: 0;
  background-color: transparent;
  border: none;
  background-image: url("../img/icons/btn-close.svg");
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer; }

.call-form-modal__header {
  margin: 0;
  margin-bottom: 16px;
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 28px;
  color: #1FC0FC;
  line-height: 34px;
  letter-spacing: -0.02em; }

.call-form-modal__lead-in {
  margin: 0;
  margin-bottom: 32px;
  font-family: "Fira Sans";
  font-weight: 400;
  font-size: 15px;
  color: #484848;
  line-height: 170%;
  opacity: 0.87; }

.call-form-modal__user-info {
  display: flex;
  flex-direction: column;
  margin-bottom: 24px; }

.call-form-modal__input-name,
.call-form-modal__input-tel {
  padding: 19px 40px 20px;
  border: 2px solid #E3E3E3;
  box-sizing: border-box;
  border-radius: 80px;
  font-family: "Fira Sans";
  font-weight: 500;
  font-size: 17px;
  line-height: 20px;
  font-variant: small-caps;
  color: #484848;
  opacity: 0.87; }
  .call-form-modal__input-name::-webkit-input-placeholder, .call-form-modal__input-tel::-webkit-input-placeholder {
    font-family: "Fira Sans";
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    font-variant: small-caps;
    color: #484848;
    opacity: 0.5; }
  .call-form-modal__input-name::-moz-placeholder, .call-form-modal__input-tel::-moz-placeholder {
    font-family: "Fira Sans";
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    font-variant: small-caps;
    color: #484848;
    opacity: 0.5; }
  .call-form-modal__input-name:-ms-input-placeholder, .call-form-modal__input-tel:-ms-input-placeholder {
    font-family: "Fira Sans";
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    font-variant: small-caps;
    color: #484848;
    opacity: 0.5; }
  .call-form-modal__input-name::-ms-input-placeholder, .call-form-modal__input-tel::-ms-input-placeholder {
    font-family: "Fira Sans";
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    font-variant: small-caps;
    color: #484848;
    opacity: 0.5; }
  .call-form-modal__input-name::placeholder,
  .call-form-modal__input-tel::placeholder {
    font-family: "Fira Sans";
    font-weight: 500;
    font-size: 17px;
    line-height: 20px;
    font-variant: small-caps;
    color: #484848;
    opacity: 0.5; }

.call-form-modal__input-name {
  margin-bottom: 8px; }

.call-form-modal__btn-wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 36px 0 50px; }

.call-form-modal__order-call-btn {
  background-image: linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%);
  padding: 19px 61px 20px;
  font-family: "Fira Sans";
  font-weight: 500;
  font-size: 17px;
  color: #FFFFFF;
  line-height: 20px;
  text-align: center;
  font-variant: small-caps;
  border: 1px solid transparent;
  border-radius: 80px;
  margin-bottom: 16px;
  box-sizing: border-box; }
  .call-form-modal__order-call-btn:active {
    background: radial-gradient(150% 119.49% at 50% 50.85%, rgba(0, 0, 0, 0) 30.67%, rgba(3, 3, 3, 0.24) 100%), linear-gradient(108.42deg, #FFC341 20.46%, #FFD701 65.31%); }

.call-form-modal__agreement-label {
  position: relative;
  margin: 0;
  padding-left: 30px;
  font-family: "Fira Sans";
  font-weight: 400;
  font-size: 13px;
  color: #484848;
  line-height: 150%;
  opacity: 0.74; }

.call-form-modal__agreement-check {
  position: absolute;
  top: 1px;
  left: 0;
  width: 16px;
  height: 16px;
  background-color: transparent;
  border: none;
  background-image: url("../img/icons/checkbox.svg");
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer; }

@media (max-width: 767px) {
  .call-modal-form {
    width: 320px;
    height: 768px;
    padding: 169px 16px; }
  .call-form-modal__btn-close {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 18px;
    height: 18px;
    font-size: 0;
    background-color: transparent;
    border: none;
    background-image: url("../img/icons/btn-close.svg");
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer; }
  .call-form-modal__header,
  .call-form-modal__lead-in {
    text-align: center; }
  .call-form-modal__btn-wrapper {
    display: flex;
    flex-direction: column;
    padding: 0 12px; }
  .call-form-modal__agreement-label {
    padding-left: 23px; } }
